import {
  Box,
  Button,
  Card,
  Divider,
  Grid,
  Hidden,
  Stack,
  Typography
} from '@mui/material'
import { Fragment, useState } from 'react'
import {
  CreateAccountForm,
  FacebookButton,
  GoogleButton,
  LoginForm
} from '../../modules/register'

import { ExpandCircleDown } from '@mui/icons-material'
import { useNavigate } from 'react-router-dom'
import ForgotPasswordButton from '../../modules/register/ForgotPasswordButton'

export default function PreRegister() {
  const [isRegister, setIsregister] = useState<boolean>(true)
  const navigate = useNavigate()

  return (
    <Fragment>
      <Box position="relative">
        <Grid
          container
          justifyContent="center"
          sx={{ minHeight: 'calc(100vh - 42px)' }}
          alignItems="center"
          // flexDirection={isRegister ? 'row' : "row-reverse"}
          spacing={4}
        >
          <Grid
            item
            xs={12}
            sm={10}
            md={5}
            xl={3}
            boxSizing="border-box"
            padding="0 30px"
          >
            <Card
              elevation={8}
              sx={{
                width: '100%',
                padding: '10% 10% 3% 10%',
                boxSizing: 'border-box'
              }}
            >
              <Stack sx={{ margin: 'auto' }} spacing={2}>
                <Box textAlign="center">
                  <Typography variant="h4">
                    {isRegister ? 'สมัครสมาชิก' : 'เข้าสู่ระบบ'}
                  </Typography>
                </Box>
                {isRegister ? (
                  <CreateAccountForm />
                ) : (
                  <LoginForm size="large" />
                )}
                <Box textAlign="center">
                  <Typography variant="body1">หรือ</Typography>
                </Box>
                <GoogleButton
                  success={() => {
                    navigate('/')
                  }}
                  size="large"
                >
                  เข้าสู่ระบบด้วย Google
                </GoogleButton>
                <FacebookButton
                  success={() => {
                    navigate('/')
                  }}
                  size="large"
                >
                  เข้าสู่ระบบด้วย Facebook
                </FacebookButton>
                <Divider />
                <Button
                  onClick={() => setIsregister(!isRegister)}
                  size="large"
                  variant="text"
                  color="primary"
                  sx={{ color: '#000' }}
                  endIcon={
                    <ExpandCircleDown sx={{ transform: 'rotate(-85deg)' }} />
                  }
                >
                  {isRegister
                    ? 'มีบัญชีอยู่แล้ว ล็อกอินที่นี่'
                    : 'ยังไม่มีบัญชี คลิกเพื่อลงทะเบียน'}
                </Button>
              </Stack>
              <ForgotPasswordButton />
            </Card>
            {/* <ForgotPasswordButton /> */}
          </Grid>
          <Grid item md={5} lg={4} xl={3} paddingTop={0}>
            <Hidden mdDown={true}>
              <img
                src={isRegister ? '/images/register.gif' : '/images/login.gif'}
                alt=""
                width="100%"
              />
            </Hidden>
          </Grid>
        </Grid>
      </Box>
    </Fragment>
  )
}
